<template>
  <div id="app">
    <header :class="{open: toggle}"
            @click="handleToggle">
      <h3 class="logo">
        <img src="./assets/image/logo.png"
             title="www.hi123.site" />Hi123.site
      </h3>
      <nav>
        <router-link to="/drama">드라마</router-link>
        <router-link to="/enter">예능</router-link>
      </nav>
      <week :type="$root.state.channel"
            :pick="pick"
            :date="date" />
    </header>
    <location :tops="$root.state.tops"
              @height="handleMarginTop" />
    <main>
      <router-view />
      <detail v-if="$root.state.item"
              :item="$root.state.item" />
    </main>
    <footer>
      <p>www.hi123.site</p>
      <p class="produce">kwange studio</p>
    </footer>
    <up />
    <loading :show="$root.state.isLoading" />
  </div>
</template>

<script>
import Week from "@/components/Week.vue";
import Detail from "@/components/Detail.vue";
import Location from "@/components/Location.vue";
import Up from "@/components/Up.vue";
import Loading from "@/components/Loading.vue";
import forbidden from "@/utils/forbidden";

export default {
  name: "App",
  components: {
    Week,
    Detail,
    Location,
    Up,
    Loading
  },
  data: function() {
    return {
      date: "all",
      toggle: false
    };
  },
  watch: {
    "$route.name": function(newVal) {
      if (newVal == "404") {
        this.$root.state.tops = [];
        return;
      }

      this.$root.state.isLoading = true;
      this.$root.state.channel = newVal;

      if (newVal == "drama") this.date = this.$root.state.dateForDrama || "all";
      if (newVal == "enter") this.date = this.$root.state.dateForEnter || "all";

      window.scrollTo(0, 0);
    },
    "$root.state.tops": function(newVal) {
      if (newVal.length <= 1) this.handleMarginTop(0);
    }
  },
  methods: {
    init() {
      this.$root.state.cw = document.documentElement.clientWidth;

      forbidden();
    },
    pick(date) {
      this.date = date;

      if (this.$root.state.channel == "drama")
        this.$root.state.dateForDrama = date;
      if (this.$root.state.channel == "enter")
        this.$root.state.dateForEnter = date;

      window.scrollTo(0, 0);
    },
    handleMarginTop(val) {
      if (this.$root.state.cw < 920) return;

      document.querySelector("main").style.marginTop = 60 + val + "px";
    },
    handleToggle() {
      if (this.$root.state.cw >= 920) return;

      this.toggle = !this.toggle;
    }
  },
  created: function() {
    if (location.hash.indexOf("enter") >= 0) this.$root.state.channel = "enter";
  },
  mounted: function() {
    this.init();
  }
};
</script>

