<template>
  <ul v-if="tops.length > 1"
      class="location"
      ref="location">
    <li :key="index"
        :class="{active: index === select}"
        v-for="(top, index) in tops"
        @click="handleClick(index, top)">
      <em>{{index + 1}}</em>
    </li>
  </ul>
</template>

<script>
import debounce from "@/utils/debounce";
import isPCBrowser from "@/utils/isPCBrowser";

const RANGE = 315;
const PC_BROWSER = isPCBrowser();

export default {
  name: "Location",
  props: {
    tops: {
      type: Array,
      default: () => []
    }
  },
  data: function() {
    return {
      select: 0
    };
  },
  methods: {
    init() {
      const vm = this;

      if (PC_BROWSER) {
        window.addEventListener("scroll", debounce(vm.handleChange, 500));
      } else {
        window.addEventListener("touchmove", debounce(vm.handleChange, 500));
      }
    },
    handleClick(index, y = 0) {
      this.select = index;
      window.scrollTo(0, y);
    },
    handleChange() {
      const Y = !PC_BROWSER
        ? window.scrollY
        : document.documentElement.scrollTop;

      this.select = this.tops.findIndex(
        top => Y - RANGE < top && top < Y + RANGE
      );
    }
  },
  mounted: function() {
    this.init();
  },
  updated: debounce(function() {
    this.$emit(
      "height",
      this.$refs.location && this.$refs.location.clientHeight
    );
  }),
  beforeDestroy: function() {
    window.removeEventListener("scroll");
  }
};
</script>

<style lang="scss">
@import "./../assets/style/var";

$w: 10px;

.location {
  position: fixed;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  padding: $w $w * 2;
  font-size: 12px;
  text-align: center;
  background: #fff;
  box-shadow: 0 2px 4px 0 $--color-400;
  opacity: 0.96;
  z-index: 2;

  li {
    display: inline-table;
    width: $w * 2;
    height: $w * 2;
    text-align: center;
    cursor: pointer;
    transition: all 120ms ease-in-out;

    & + li {
      margin-left: $w;
    }

    &:hover {
      color: $--color-100;
      font-weight: 600;
    }

    &.active {
      color: #fff;
      background-color: $--color-100;
    }

    em {
      @include middle();
    }
  }
}
</style>
