<template>
  <div class="card">
    <a :title="title">
      <span :data-ep="ep ? ep.text.replace(/^\s+|\s+$/gm,'') : ''">
        <label><em>N</em></label>
        <img height="264"
             :alt="title"
             :src="img">
        <i></i>
      </span>
      <strong>{{title}}</strong>
    </a>
    <span><em>{{time}}</em></span>
  </div>
</template>

<script>
export default {
  name: "Card",
  props: ["title", "img", "time", "ep"]
};
</script>

<style lang="scss">
@import "./../assets/style/var";

.card {
  display: inline-block;
  width: $card-width;
  margin-bottom: $space;
  text-align: center;

  a {
    display: block;
    text-decoration: none;
    cursor: pointer;

    span {
      position: relative;
      display: block;
      height: $card-height;
      background-color: $--color-500;
      overflow: hidden;

      img {
        display: block;
        width: 100%;
        object-fit: cover;
        transition: -webkit-transform 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
        transition: transform 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
      }

      &::after {
        position: absolute;
        left: 0;
        bottom: 0;
        content: attr(data-ep);
        width: 100%;
        height: 35px;
        line-height: 35px;
        padding-top: 89px;
        text-align: center;
        color: #fff;
        background: url(./../assets/image/mask.png) repeat-x 0 62px;
        @include ell();
        z-index: 1;
      }

      i {
        position: absolute;
        top: 0;
        left: 0;
        width: $card-width;
        height: $card-height;
        border: 1px solid $--color-200;
        opacity: 0.098;
      }
    }

    &:hover img {
      transform: scale(1.05);
    }

    &:hover strong {
      color: $--color-100;
      text-shadow: 0 1px 2px $--color-400;
    }
  }

  strong {
    display: block;
    height: 35px;
    line-height: 35px;
    font-size: 16px;
    color: $--color-200;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  & > span {
    display: table;
    margin: 0 auto;
    font-size: 12px;
    color: $--color-300;
    cursor: default;

    em {
      @include middle();
    }
  }

  label {
    display: none;
  }

  .new label {
    display: table;
    position: absolute;
    top: 10px;
    right: 10px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: $--color-100;
    z-index: 1;

    em {
      @include middle();
    }
  }
}
</style>
