<template>
  <a class="up"
     @click="handleClick"></a>
</template>

<script>
export default {
  name: "Up",
  methods: {
    handleClick() {
      window.scrollTo(0, 0);
    }
  }
};
</script>

<style lang="scss">
@import "./../assets/style/var";

.up {
  position: fixed;
  right: $space;
  bottom: 26px;
  border-radius: 50%;
  box-shadow: 0 1px 2px $--color-300;
  overflow: hidden;
  z-index: 1;

  &::after {
    display: block;
    content: "";
    width: 50px;
    height: 50px;
    background: #fff url(./../assets/image/arrow.png) no-repeat center / 40%;
    transition: all 100ms ease-in-out;
  }

  &:hover::after {
    background: $--color-100 url(./../assets/image/arrow-hover.png) no-repeat
      center / 40%;
    cursor: pointer;
  }
}
</style>
