import Card from "@/components/Card.vue";

export default function(watchVal, MAP) {
  const CARD_W = 185;
  const CARD_H = 315;
  const SPACE = 20;
  const CARD_W_SM = 92.5;

  return {
    components: {
      Card
    },
    data: function() {
      return {
        list: []
      };
    },
    watch: {
      [watchVal]: function(newVal) {
        this.list = MAP[newVal].filter(item => item.episodes.length !== 0);

        this.$nextTick(() => {
          this.layout();
        });
      }
    },
    methods: {
      init() {
        this.list = MAP["all"].filter(item => item.episodes.length !== 0);
      },
      ready() {
        this.layout();
        this.$root.state.isLoading = false;
        window.onorientationchange = () => {
          setTimeout(() => {
            this.layout();
          });
        };
      },
      layout() {
        const MQ = this.$root.state.cw < 920; // is media query or not
        const wrapper = this.$refs.wrapper;
        const wrapperW = wrapper.clientWidth - SPACE * 2.65;
        const cards = Array.from(wrapper.children);
        const colCount = Math.floor(wrapperW / (!MQ ? CARD_W : CARD_W_SM));
        const marginRight =
          (wrapperW - colCount * (!MQ ? CARD_W : CARD_W_SM)) / (colCount - 1);

        cards.forEach((card, index) => {
          if (index === 0 || (index + 1) % colCount !== 0) {
            card.style.marginRight = marginRight + "px";
          } else {
            card.style.marginRight = "0px";
          }

          this.addNewLabel(card);
        });

        !MQ &&
          (this.$root.state.tops = Array(Math.ceil(cards.length / colCount))
            .fill(CARD_H + SPACE)
            .map((n, i) => n * i));
      },
      addNewLabel(card) {
        const [a, span] = Array.from(card.children);
        const diffDay =
          ((new Date() - new Date(span.innerText)) / 86400000) | 0;

        if (diffDay <= 1) {
          a.children[0].className = "new";
        } else {
          a.children[0].className = "";
        }
      },
      handleSelect(item) {
        this.$root.state.item = item;
      }
    },
    beforeRouteEnter: function(to, from, next) {
      next(function(vm) {
        const date =
          vm.$root.state.channel == "drama"
            ? vm.$root.state.dateForDrama
            : vm.$root.state.dateForEnter;

        vm.list = MAP[date || "all"].filter(item => item.episodes.length !== 0);

        vm.$nextTick(() => {
          vm.layout();
        });
      });
    },
    created: function() {
      this.init();
    },
    mounted: function() {
      this.ready();
    },
    beforeDestroy: function() {
      window.onorientationchange = null;
    }
  };
}
